body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.about-me{
    display: flex;
    width: 100%;
    height: 80vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #f5f5f5;
}
.about-me-inside{
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: space-around;
}
.about-left{
    display: flex;
    width: 45%;
    flex-direction: column;
}
.about-left h2{
    font-size: 40px;
    font-weight: 400;
    line-height: 64px;
    text-transform: uppercase;
    text-align: justify;
}
.about-left p{
    font-size: 30px;
}
.about-right{
    display: flex;
    width: 35%;
    flex-direction: column;
}
.data{
    display: flex;
    width: 100%;
    
    justify-content: space-between;
}
.data h6, p {
    text-align: justify;
}
h6{
    color: rgb(180, 43, 43);
}
.block-info{
    display: flex;
    width: 60%;
    height: 150px;
    border: 1px solid;
}
.block-info-left{
    display: flex;
    width: 33%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-right:  1px solid;
}
.block-info h2{
    margin: 0%;
}
.block-info h4{
    text-transform: uppercase;
}
.block-info-center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 33%;
    color: white;
    background-color: rgb(27, 22, 22);

}
.block-info-right{
    display: flex;
    width: 33%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 1px solid;
}

.header{
    display: flex;
    width: 100%;
    height: 10vh;
    background-color:  #f5f5f5;
}
.header-inside{
    display: flex;
    width: 100%;
    height: 10vh;
    align-items: center;
    justify-content: space-around;
    
}
.logo{
    display: flex;
    width: 50%;
    align-items: center;
    justify-content: space-around;
}

.logo p{
    font-size: 20px;
    padding: 0%;
    margin: 0%;
    text-transform: uppercase;
    font-weight: bold;
}
.main{
    display: flex;
    background-color: #f5f5f5;
    width: 100%;
    height: 100vh;
    justify-content: space-around;
}
.main-inside{
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: space-between;
}
.main-inside-left{
    display: flex;
    width: 45%;
    flex-direction: column;

}
.main-inside-right{
    display: flex;
    width: 40%;
    
    
}
h2{
    font-size: 56px;
    font-weight: 400;
    line-height: 64px;
    text-align: justify;
}

.portfolio{
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #f5f5f5;

}

.gallery{
    width: 85vw;
    height: 60%;
    display: flex;
    grid-gap: 5px;
    gap: 5px;
    background-image: url(/src/Icons/BRABU1S.png);
    
}


.gallery img{
    width: 100%;
    height: 80%;
    flex: 1 1;
    object-fit: cover;
    overflow: hidden;
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.3s;
}
.gallery img:hover{
    flex: 1 1;
    height: 90%;
}  

.back{
    width: 1000px;
    height: 900px;
}
section{
    display: flex;
    width: 100%;
    height: 20vh;
    flex-direction: column;
    background-color: #f5f5f5;
}
.rotate{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 6vh;
    background-color: rgb(212, 211, 211);
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
}
.rotate h3{
    font-size: 30px;
    font-weight: 800;
    text-transform: uppercase;
    color: rgb(180, 43, 43);
}
.skills{
    display: flex;
    width: 100%;
    height: 130vh;
    flex-direction: column;
    align-items: center;
    background-color: #f5f5f5;
}

.skills-card{
    display: flex;
    width: 100%;
    height: 50vh;
    background-color: #f5f5f5;
}
.skills-card-inside{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
}
.html{
    fill: #e34c26
    ;
}
.css{
    fill: #264de4
    ;
}
.js{
    fill: #f0db4f;
}
.react{
    fill: #61DBFB;
}
.icon-bar {
    position: fixed;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  .icon-bar a {
    display: block;
    text-align: center;
    padding: 16px;
    transition: all 0.3s ease;
    color: white;
    font-size: 20px;
  }
  
  .icon-bar a:hover {
    padding: 20px;
  }
  
  .telegram {
    background: #0088CC;
    color: white;
  }
  
  .github {
    background: #333;
    color: white;
  }
  
  .google {
    background: #dd4b39;
    color: white;
  }
  
  .linkedin {
    background: #007bb5;
    color: white;
  }
  
  .instagram {
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
    color: white;
  }
  
  .content {
    margin-left: 75px;
    font-size: 30px;
  }
