.header{
    display: flex;
    width: 100%;
    height: 10vh;
    background-color:  #f5f5f5;
}
.header-inside{
    display: flex;
    width: 100%;
    height: 10vh;
    align-items: center;
    justify-content: space-around;
    
}
.logo{
    display: flex;
    width: 50%;
    align-items: center;
    justify-content: space-around;
}

.logo p{
    font-size: 20px;
    padding: 0%;
    margin: 0%;
    text-transform: uppercase;
    font-weight: bold;
}