.about-me{
    display: flex;
    width: 100%;
    height: 80vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #f5f5f5;
}
.about-me-inside{
    display: flex;
    width: 80%;
    align-items: center;
    justify-content: space-around;
}
.about-left{
    display: flex;
    width: 45%;
    flex-direction: column;
}
.about-left h2{
    font-size: 40px;
    font-weight: 400;
    line-height: 64px;
    text-transform: uppercase;
    text-align: justify;
}
.about-left p{
    font-size: 30px;
}
.about-right{
    display: flex;
    width: 35%;
    flex-direction: column;
}
.data{
    display: flex;
    width: 100%;
    
    justify-content: space-between;
}
.data h6, p {
    text-align: justify;
}
h6{
    color: rgb(180, 43, 43);
}
.block-info{
    display: flex;
    width: 60%;
    height: 150px;
    border: 1px solid;
}
.block-info-left{
    display: flex;
    width: 33%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-right:  1px solid;
}
.block-info h2{
    margin: 0%;
}
.block-info h4{
    text-transform: uppercase;
}
.block-info-center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 33%;
    color: white;
    background-color: rgb(27, 22, 22);

}
.block-info-right{
    display: flex;
    width: 33%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-left: 1px solid;
}
