.portfolio{
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #f5f5f5;

}

.gallery{
    width: 85vw;
    height: 60%;
    display: flex;
    gap: 5px;
    background-image: url(/src/Icons/BRABU1S.png);
    
}


.gallery img{
    width: 100%;
    height: 80%;
    flex: 1;
    object-fit: cover;
    overflow: hidden;
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.3s;
}
.gallery img:hover{
    flex: 1;
    height: 90%;
}  

.back{
    width: 1000px;
    height: 900px;
}