.main{
    display: flex;
    background-color: #f5f5f5;
    width: 100%;
    height: 100vh;
    justify-content: space-around;
}
.main-inside{
    display: flex;
    width: 90%;
    align-items: center;
    justify-content: space-between;
}
.main-inside-left{
    display: flex;
    width: 45%;
    flex-direction: column;

}
.main-inside-right{
    display: flex;
    width: 40%;
    
    
}
h2{
    font-size: 56px;
    font-weight: 400;
    line-height: 64px;
    text-align: justify;
}
