section{
    display: flex;
    width: 100%;
    height: 20vh;
    flex-direction: column;
    background-color: #f5f5f5;
}
.rotate{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 6vh;
    background-color: rgb(212, 211, 211);
    transform: rotate(-6deg);
}
.rotate h3{
    font-size: 30px;
    font-weight: 800;
    text-transform: uppercase;
    color: rgb(180, 43, 43);
}